/* start variablles */
:root{
  --main-color:#10cab7;
  --secondary-color:#2c4755;
  --section-padding:60px;
  --section-background:#f6f6f6;
}
/* end variables */
*{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
body{
  font-family: "work sans",sans-serif;
}
html{
  scroll-behavior: smooth;
}
/* end global rules */

.container{
  padding: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  width:100%;
  margin: auto;

 
}
@media(min-width:768px){
  .container{
      width: 75s0px;
  }
}
@media(min-width: 992px){
  .container{
      width: 1970px;
  }
}
@media(min-width:1200px){
  .container{
      width: 1170px;
  }
}
/* start components */
.special-heading{
  color: #ebeced;
  font-size: 100px;
  text-align: center;
  font-weight: 800;
 
   margin: 0;
   letter-spacing: -3;
  

}
.special-heading +p{
  margin: -30px 0 0;
  text-align: center;
  font-size: 20px;

color: #797979;}
@media (max-width : 767px) {
.special-heading{
  font-size: 60px;}
  .special-heading +p{
      margin-top: -20px;
  }
}

  

/* end components */
/*header*/

.header   {
background-color:rgb(207, 204, 204);
 
  height: 80%;
  width: 100%;
  box-sizing: 30px;
 
  border-color: transparent transparent transparent;


 color: #f6f6f6;

}
.header .links ul { display: flex;
align-items: center;
text-decoration: none;
justify-content: space-between;
margin-left: 30px;

list-style: none;}
.logo{
  width: 100px;

}
a{
  text-decoration: none;
  color: #f6f6f6;;
  font-size: 18px;

}




.landing{
  background-image: url(./components/image//montain2.jpg);
  background-size: cover;
 height: 100vh;
 height: calc(100vh-64px);
 position: relative;
}
.landing .intro-text{

  position: absolute;
left: 50%;
top: 50%;
text-align: center;
transform: translate(-50% ,-50%);
width: 320px;
max-width: 100%;

}
.landing .intro-text h1{
margin: 0;
color: var(--main-color);
font-size: 50px;
font-weight: bold;
text-align: center;

}
.landing .intro-text p{
font-size: 19px;
line-height: 1.8;
}
/*  start features */
.features{
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #f6f6f6;
}
.features .container{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
grid-gap: 20px;
}
.features .feat{
  padding: 20px;
  text-align: center;
}
.features .feat i{

  color: var(--main-color);
}
.features .feat h3{
font-weight: 800;
}
.features .feat p{
line-height: 1.8;
color: #777;
font-size: 17px;
}
/* end features */
/* start services */
.services{
  padding-top: 60px;
  padding-bottom: 60px;
}
.services .services-content{
display: grid;
grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
grid-gap: 30px;
margin-top: 100px;
}
.services .services-content .serv{
  display: flex;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .services .services-content .serv{
      flex-direction: column;
      text-align: center;
  }
}
.services .services-content .serv i {
color: var(--main-color);
flex-basis: 60px;
}
.services .services-content .serv .text{
   flex: 1;/* bch yekhou be9i l espace w ykhali 60px */
}
.services .services-content .serv .text h3{
margin: 0 0 20px; 
}
.services .services-content .serv .text p{
  color: #444;
  font-weight: 300;
  line-height: 1.6;

}
.services .services-content  .image{
  text-align: center;
  position: relative;
}
.services .services-content .image:before{
  content: "";
  background-color: var(--secondary-color);
  width: 100px;
  height: calc(100% + 100px);
  top: -50px;
  position: absolute;
  right: 0;
  z-index: -1;
}
.services .services-content  .image img{
  width: 260px;
}
/* end services */
@media(max-width : 1199px){
  .image .image-column{
      display: none;
  }
}
/* start portfoli */
.portfolio{
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);

  background-color: var(--section-background)
}
.portfolio .portfolio-content{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
  grid-gap: 30px;
  margin-top: 80px;
  }
  .portfolio .portfolio-content .card{
background-color: white;
  }
  .portfolio .portfolio-content .card img{
      width: 350px;
  }
  .portfolio .portfolio-content .card .info{
      padding: 20px;
  }
  .portfolio .portfolio-content .card .info h3{
      margin: 0;

  }
  .portfolio .portfolio-content .card .info p{
      color: #777;
      line-height: 1.6;
      margin-bottom: 0;

  }
  /* start about */
  .about{
      padding-top: var(--section-padding);
      padding-bottom: var(--section-padding) ;
  }
  .about-content{
      margin-top: 100px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
  }
  .about-content .image{
      position: relative;
      width: 250px;
      height: 375px;
  }
  @media(max-width: 991px){
      .about-content .image{
          margin: 0 auto 60px;
      }
  }
  .about-content .image::before{
content: "";
position: absolute;
background-color: #ebeced;
width: 80px;
height: calc(100% + 80px);
top: -40px;
left: -30px;
z-index:-1;

  }

  .about-content .image:after{
top: -40px;
content: "";
position: absolute;
width: 120px;
height: 300px;
border-left: 80px solid var(--main-color);
border-bottom: 80px solid var(--main-color);
z-index: -1;
right: -150px;
  }
  @media(max-width: 991px){
      .about-content .image::before,
      .about-content .image:after{display: none;}

  }
 .about .about-content .text{
      /* flex-basis: calc(100% - 500px); */
      flex: 1;
      padding-left: 200px;
  }
  .about-content .text p:first-of-type{
      font-weight: bold;
      line-height: 2;
      border-bottom: 50px;
  }
  .about-content .text hr{
      width: 50%;
      display: inline-block;
      border-color: var(--main-color);

  }
  .about-content .text p:last-of-type{
color: #777;
line-height: 2;
  }
  @media(max-width: 991px) {
      .about .about-content{
          flex-direction: column;
          text-align: center;
      }
  }

  .contact{
      padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);

  background-color: var(--section-background)
  }
.contact .info{
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
text-align: center;
}
.contact .info .label{
  font-size: 35px;
  font-weight: 800;
  color: var(--secondary-color);
letter-spacing: -2px;
margin-bottom: 15px;
}
.contact .info .link{
  display: block;
  font-size: 35px;
text-decoration: none;
color: var(--main-color);
}
.contact .info .social{
  display: flex;
justify-content: center;
  margin-top: 20px;
  font-size: 16px;
}
.contact .info .social i{
  margin-left: 10px;
  color: var(--secondary-color);
}
@media (max-width:767px){
  .contact .info .label ,
  .contact .info .label{
      font-size: 25px;
  }

}

    /* end about */
    /* start footer */
    .footer{
      background-color: var(--secondary-color);
      color: white;
      padding: 30px 10px;
      text-align: center;
      justify-content: center;
      font-size: 18px;

  }
  .footer span{
      font-weight: bold;
color: var(--main-color);
  }
      /*end footer */